/* CreateTable.css */

/* Table styling */
.createTable-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.createTable-table th, .createTable-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.createTable-table th {
    background-color: #f2f2f2;
    font-weight: bold;
}

.createTable-table tbody tr:nth-child(even) {
    background-color: #f9f9f9;
}
